import React from "react";
import { useStaticQuery, graphql, navigate } from "gatsby";
import Img from "gatsby-image";

import Helmet from "../components/Helmet";

export default () => {
    const data = useStaticQuery(graphql`
        query {
            file(relativePath: { eq: "goodbye.png" }) {
                childImageSharp {
                    fluid {
                        ...GatsbyImageSharpFluid_noBase64
                    }
                }
            }
        }
    `);

    return (
        <div css={{
            maxWidth: 720,
            margin: "0 auto",
            padding: 25,
            "p a": {
                color: "var(--green)",
            },
        }}>
            <Helmet
                title="Sayonara"
                description="We're sorry to see you go. Did you have any issues? Were we missing something? Please share your feedback so that we can improve ourselves."
                robots="none"
            />
            <Img
                fluid={ data.file.childImageSharp.fluid }
                style={{
                    margin: "0 auto",
                    maxWidth: 450,
                }}
            />
            <div css={{
                textAlign: "center",
            }}>
                <div css={{
                    fontSize: "2em",
                    fontWeight: 700,
                    marginBottom: 25,
                }}>
                    Sayonara!
                </div>
                <div css={{
                    marginBottom: 25,
                    color: "lightgrey",
                    fontWeight: 500,
                }}>
                    We're sorry to see you go. Did you have any issues? Were we missing something? Please share your feedback so that we can improve ourselves.
                </div>
                <div>
                    <button
                        css={{
                            fontWeight: 500,
                            backgroundColor: "var(--green)",
                            boxShadow: "0px 15px 50px -10px black",
                            ":active": {
                                filter: "saturate(80%)",
                            },
                        }}
                        onClick={ () => navigate("/glee") }
                    >
                        Share Feedback
                    </button>
                </div>
            </div>
        </div>
    );
};
